import { Link } from "gatsby"

export default function NavLinks ({ isVisible }) {
  const navItems = [
    {
      name: "Home",
      link: "/",
    },{
      name: "Transmisso",
      link: "/rt",
    },{
      name: "Sobre",
      link: "/sobre"
    },
  ];

  return (
    <ul className={ `navbar ${isVisible ? 'visible' : ''}`} >
      {navItems.map(item => (
        <li key={item.link}>
          <Link to={item.link} className={`navbar__item`}>
            {item.name}
          </Link>
        </li>
      ))}
    </ul>
  )
}